:local(.App) {
	margin: 3rem auto 2rem auto;
}

:local(.App .Title ){
	color: #222;
}

:local(.App hr) {
	margin: 1.5rem 0;
	border-style: solid;
	border-width: 0 0 1px 0;
	border-color: #DADADA;
}

:local(.NavBar) {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}

:local(.CartTextLink) {
	display: -ms-flexbox;
	display: flex;
}

:local(.CartTextLink:hover p) {
	color: #407FC0;
}

:local(.CartTextLink:disabled) {
	opacity: .3;
	cursor: not-allowed;
}
:local( .CartTextLink:disabled:hover){
	opacity: .3;
	cursor: not-allowed;
}

:local(.CartTextLink:disabled p) {
	color: #292929;
}
:local( .CartTextLink:disabled:hover p){
	color: #292929;

}

:local(.CartTextLink p) {
	display: inline;
	margin: 0 0 0 6px;
	font-size: 14px;
	color: #292929;
}

:local(.CartModalWrapper) {
	position: absolute;
	width: 100%;
	z-index: 500;
	top: 0;
	-webkit-transition: all 300ms ease;
	-o-transition: all 300ms ease;
	transition: all 300ms ease;
}

:local(.cartModalBackdropWrapper) {
	z-index: 100;
	-webkit-transition: all 300ms ease;
	-o-transition: all 300ms ease;
	transition: all 300ms ease;
}

:local(.cartModalBackdrop) {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: #f4f5f7;
}

/* MEDIA QUERIES */
@media screen and (min-width: 1200px) {
	:local(.App) {
		width: 1120px;
	}
}

@media screen and (max-width: 1199px) {
	:local(.App) {
		width: 95%;
	}
}

@media screen and (max-width: 768px) {
	:local(.App) {
		width: 90%;
		max-width: 668px;
	}
}

@media screen and (max-width: 420px) {
	:local(.NavBar ){
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: start;
		    align-items: flex-start;
	}

	:local(.CartTextLink) {
		margin-top: .5rem;
	}
}

@media screen and (max-width: 320px) {
	:local(.App ){
		margin: 2rem auto 1rem auto;
		max-width: 285px;
	}
}



